<template>
	<div>
		<wv-checklist title="" :options="options" v-model="checkedItems"></wv-checklist>
	</div>
</template>

<script type="text/babel">
export default {
  data () {
    return {
      options: [
        {
          label: '1',
          value: 'value1'
        },
        {
          label: '2',
          value: 'value2'
        },
        {
          label: '3',
          value: 'value3'
        },
        {
          label: '4',
          value: 'value4',
          disabled: true
        }
      ],
      checkedItems: ['value1', 'value3']
    }
  }
}
</script>



