<template>
  <div class="page page-with-padding buttons">
    <wv-button type="default" @click.native="showDialog('ios', '')">iOS Dialog </wv-button>
    <wv-button type="default" @click.native="showDialog('ios')">iOS Dialog </wv-button>
    <wv-button type="default" @click.native="showDialog('android', '')">Android Dialog </wv-button>
    <wv-button type="default" @click.native="showDialog('android')">Android Dialog </wv-button>
  </div>
</template>

<script type="text/babel">
import Dialog from '../../src/components/dialog'

export default {
  methods: {
    doSomething () {
      console.log('hello')
    },

    showDialog (skin, title) {
      Dialog({
        title: title,
        message: 'hello world',
        skin
      },
      this.doSomething)
    }
  }
}
</script>

<style scoped lang="scss">
  .buttons {
    display: block;
    margin: 20px auto;
    padding: 0 30px;
  }
</style>



