<template>
  <div>
    <wv-group title="">
      <wv-input label="label" placeholder="" v-model="valueText"></wv-input>
      <wv-input label="label" placeholder="" type="number" v-model="valueNumber"></wv-input>
    </wv-group>
    <wv-group title="">
      <wv-input label="label" placeholder="" :readonly="true" v-model="valueReadonly"></wv-input>
    </wv-group>
    <wv-group title="">
      <wv-input placeholder="" v-model="valueText"></wv-input>
    </wv-group>
  </div>
</template>

<script type="text/babel">
export default {
  data () {
    return {
      valueText: 'hello we-vue',
      valueNumber: '110',
      valueReadonly: ''
    }
  }
}
</script>



