<template>
  <div class="page-with-padding">
    <wv-button type="primary" @click.native="showTips"> (5S)</wv-button>
  </div>
</template>

<script type="text/babel">
import TopTips from '../../src/components/top-tips'

export default {
  methods: {
    showTips () {
      TopTips({
        message: '',
        duration: 5000
      })
    }
  }
}
</script>



