<template>
  <wv-navbar fixed v-model="selected" @change="changed">
    <wv-navbar-item id="item1">1</wv-navbar-item>
    <wv-navbar-item id="item2">2</wv-navbar-item>
    <wv-navbar-item id="item3">3</wv-navbar-item>
  </wv-navbar>
</template>

<script type="text/babel">
export default {
  data () {
    return {
      selected: 'item1'
    }
  },

  methods: {
    changed (e) {
      console.log(e)
    }
  }
}
</script>

<style scoped lang="scss">

</style>



