<template>
  <div class="page-with-padding">
    <wv-button type="primary" @click.native="showSuccess"></wv-button>
    <wv-button type="primary" @click.native="showError"></wv-button>
  </div>
</template>

<script type="text/babel">
import Toast from '../../src/components/toast'

export default {
  methods: {
    showSuccess () {
      Toast('')
    },

    showError () {
      Toast({
        duration: 2000,
        message: '',
        icon: 'warn'
      })
    }
  }
}
</script>



