<template>
  <div class="page">
    <wv-panel title="">
      <wv-media-box :thumb="thumb" title="" :description="description" to="/"></wv-media-box>
      <wv-media-box :thumb="thumb" title="" :description="description" to="/"></wv-media-box>

      <wv-cell title="" is-link slot="ft"></wv-cell>
    </wv-panel>

    <wv-panel title="">
      <wv-media-box :thumb="thumb" title="" :description="description" to="/" type="text"></wv-media-box>
      <wv-media-box :thumb="thumb" title="" :description="description" to="/" type="text"></wv-media-box>

      <wv-cell title="" is-link slot="ft"></wv-cell>
    </wv-panel>

    <wv-panel title="">
      <wv-cell title="" is-link to="/">
        <img :src="thumbSmall" alt="" slot="icon" class="cell-icon">
      </wv-cell>
      <wv-cell title="" is-link to="/">
        <img :src="thumbSmall" alt="" slot="icon" class="cell-icon">
      </wv-cell>
    </wv-panel>

    <wv-panel title="">
      <wv-media-box :thumb="thumb" title="" :description="description" to="/" type="text">
        <ul class="weui-media-box__info" slot="box_ft">
          <li class="weui-media-box__info__meta"></li>
          <li class="weui-media-box__info__meta"></li>
          <li class="weui-media-box__info__meta weui-media-box__info__meta_extra"></li>
        </ul>
      </wv-media-box>
    </wv-panel>
  </div>
</template>

<script type="text/babel">
import thumb from '../assets/images/wevue_placeholder.png'
import thumbSmall from '../assets/images/icon_tabbar.png'
const description = 'we-vue  vue.js  weui '

export default {
  data () {
    return {
      thumb,
      thumbSmall,
      description
    }
  }
}
</script>

<style scoped lang="scss">
  .cell-icon {
    display: block;
    width: 20px;
    margin-right: 5px;
  }
</style>



